.workEpContainer {
    padding: 20px;
    position: relative;
}

.companies {
    position: relative;
    max-width: 1200px;
    margin: 0 auto;
}


.companies>.cgu {
    margin-top: 50px;
}

.companies>.kerson {
    margin-top: 50px;
}

.company {
    max-width: 400px;
    color: var(--main-text-color);
    box-sizing: border-box;
    position: relative;
}

.company .title {
    font-size: 20px;
    font-weight: bold;
}

.company .position {
    margin: 10px 0;
}

.company .companyContent {
    padding: 20px;
    border-radius: 20px;
    border: 4px solid var(--neon-green);
    box-sizing: border-box;
    box-shadow: var(--neon-green-text-shadow-small)
}

.companyContainer {
    display: flex;
    height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    box-sizing: border-box;
}

.companyContainer .workTimeline {
    position: absolute;
    top: 0;
    left: 0;
    width: 150px;
    height: 100%;
}

.companyContainer .workTimeline .stick {
    height: 100%;
    width: 10px;
    background: var(--neon-pink);
    position: relative;
    box-shadow: var(--neon-stick);
    z-index: 1;
}

.companyContainer .workTimeline .freeTimeline {
    height: 50px;
    border-left: 1px dashed var(--project-connect-timeline-color);
    margin-left: 4px;
    position: absolute;
    top: 100%;
    left: 0;
}

.companyContainer .workTimeline .topDot {
    width: 20px;
    height: 20px;
    background: var(--neon-yellow);
    content: '';
    position: absolute;
    border-radius: 50%;
    top: -5px;
    left: -5px;
    transform-origin: center;
    box-shadow: var(--neon-yellow-text-shadow);
    z-index: 2;
}

.companyContainer .workTimeline .bottomDot {
    width: 20px;
    height: 20px;
    background: var(--neon-yellow);
    content: '';
    position: absolute;
    border-radius: 50%;
    bottom: -5px;
    left: -5px;
    transform-origin: center;
    box-shadow: var(--neon-yellow-text-shadow);
    z-index: 2;
}

.companyContainer .workTimeline .topDate,
.companyContainer .workTimeline .bottomDate {
    color: var(--project-date-color);
    text-shadow: var(--neon-green-text-shadow);
}

.companyContainer .workTimeline>span:first-of-type {
    content: '';
    position: absolute;
    left: 20px;
    top: 0;
}

.companyContainer .workTimeline>span:last-of-type {
    content: '';
    position: absolute;
    left: 20px;
    bottom: 0;
}


.linkToTimeline {
    border-top: 4px dotted var(--neon-blue);
    box-shadow: var(--neon-blue-text-shadow);
    z-index: 0;
    min-width: 100px;
}

.catchpoint .linkToTimeline {
    width: 200px;
}

.cgu .linkToTimeline {
    width: calc(200px + 15%);
}

.kerson .linkToTimeline {
    width: calc(200px + 30%);
}